import React from 'react';

import Navbar from '../components/navbar.js';
import Footer from '../components/footer.js';

import '../styles/CSS/swarup.min.css';


const Layout = (props) => {
    return (
        <div>
            <Navbar />
                <div style={{ minHeight:'100vh' }}>
                    {props.children}
                </div>
            <Footer />
        </div>
    )
}

export default Layout;