import React from 'react';

import { Link } from 'gatsby';

const Navbar = () => {
    return (
        <nav className="sw-navbar">
        
        <Link to='/' className="sw-navbar-logo sw-navbar-link f-28 py-1 b-1">Swarup<span className="f-10 ml-5px">v1.0</span></Link> 

        <div className="sw-navbar-list ml-10px mt-1">
            <div className="sw-navbar-item ">
                <Link to='/documentation' className="sw-navbar-link sans-serif f-14 mx-5px">Documentation</Link>
            </div>
            <div className="sw-navbar-item ">
                <Link className="sw-navbar-link sans-serif f-14 mx-5px">Components</Link>
            </div>
            <div className="sw-navbar-item">
                <Link className="sw-navbar-link sans-serif f-14 mx-5px">Fonts</Link>
            </div>
            <div className="sw-navbar-item">
                <Link className="sw-navbar-link sans-serif f-14 mx-5px">Spacing</Link>
            </div>
            <div className="sw-navbar-item">
                <Link className="sw-navbar-link sans-serif f-14 mx-5px">Utilities</Link>
            </div>
            <div className="sw-navbar-item">
                <Link className="sw-navbar-link sans-serif f-14 mx-5px">About</Link>
            </div>
        </div>

        <div className="sw-navbar-list pos-absolute right">
            <div className="sw-navbar-link">
                <div className="sw-navbar-item sans-serif f-14 mx-5px">
                    <button className="sw-btn sw-btn-invert">
                        Download
                    </button>
                </div>
            </div>
        </div>

    </nav>
    )
}

export default Navbar;