import React from "react"
import { Link } from 'gatsby';

import Layout from '../components/layout.js';

import hljs from 'highlight.js/lib/highlight';
import javascript from 'highlight.js/lib/languages/javascript';

hljs.registerLanguage('javascript', javascript);

hljs.initHighlightingOnLoad();

const Index = () => {
  return (
    <Layout>
      <div className="sw-container pt-5">
          <div>
              <div className="sw-row">
                <div className="sw-col-12">
                  <h1 className="f-64 w-75 b-1 sans-serif mb-0">Build theme-based, responsive sites <br/> with Swarup</h1>
                  <p className="f-28 w-50">
                      Quickly design and customize responsive theme-based sites with Swarup,
                      the front-end framework featuring bunch of prebuild components.
                  </p>

                  <Link className="sw-btn p-3 px-4 f-20" to="/documentation/">Get Started</Link>
                  <a href="http://cdn.jsdelivr.net/gh/Ajay1290/Swarup/static/dist/CSS/swarup.min.css" target="_blank" download 
                  className="sw-btn sw-btn-outline p-3 ml-2 px-4 f-20" rel="noreferrer">Download</a>

                  <div className="mt-2">Currently v1.0</div>
                </div>
              </div>

              <div className="sw-row my-100px">

                <div className="sw-col-6">
                  <h2 className="f-36 b-2 sans-serif mb-0 ">Installation</h2>
                  <h5 className="f-18 b-1 mt-4">
                    When you are in hurry and want to make work done quickly, <br />
                    you only need to include Swarup’s compiled CSS or JS, <br />
                    by using SwarupCDN.
                  </h5>
                  <Link className="sw-btn sw-btn-outline p-3 px-4 f-16" to="/documentation/">Read Installation Docs</Link>
                </div>

                <div className="sw-col-6">
                    <div className="my-2">
                      <figure>
                        <pre className="p-5 f-12" style={{ width:'110%', backgroundColor:'#e9e9e9', borderRadius:'10px', marginTop:'5em' }}>
                          <code>
                            # Via NPM <br/>
                            npm install swarup <br/> <br/>
                          </code>
                        </pre>
                      </figure>
                    </div>
                </div>

              </div>

              <div className="sw-row my-100px py-3">

                <div className="sw-col-6">
                  <h2 className="f-36 b-2 sans-serif mb-0">SwarupCDN</h2>
                  <h5 className="f-18 b-1 mt-4">
                    When you are in hurry and want to make work done quickly, <br />
                    you only need to include Swarup’s compiled CSS or JS, <br />
                    by using SwarupCDN.
                  </h5>
                  <Link className="sw-btn sw-btn-outline p-3 px-4 f-16" to="/documentation/">Explore Docs</Link>
                </div>

                <div className="sw-col-6">
                  <div className="my-2">
                    <pre className="p-5 f-12" style={{ overflowX:'scroll', width:'110%', backgroundColor:'#e9e9e9', borderRadius:'10px', marginTop:'5em' }}>
                        <code class="language-html" >
                          
                          &lt;!-- Swarup CSS Only --&gt; <br/>
                          &lt; link rel="stylesheet" href="https://cdn.jsdelivr.net/gh/Ajay1290/Swarup@master/static/dist/CSS/swarup.min.css"/ &gt; <br/> <br/>

                          &lt;!-- Swarup JS Only --&gt; <br/>
                          &lt; script src="https://cdn.jsdelivr.net/gh/Ajay1290/Swarup@master/static/dist/JS/swarup.min.js" &gt; &lt; /script &gt;
                        </code>
                    </pre>
                  </div>
                </div>

              </div>

          </div>
      </div>
    </Layout>
  )
}

export default Index;

