import React from 'react';

import { Link } from 'gatsby';

const Footer = () => {
    return (
        <div className="sw-bg-secondary pt-5 " >
            <div className='sw-container' style={{ color:"#fff" }}>
                <div className='sw-row'>
                    <div className="sw-col-3">
                        <h1 className='m-0 p-0'>Swarup</h1>
                        <h5 className='mt-3 b-1 sans-serif f-14'>
                            The UI/UX Framework to build theme base fully responsive and text-friendly websites
                        </h5>
                    </div>
                    <div className='sw-col-6 px-0 '>
                        <nav className='sw-navbar text-right mx-0 px-0'>
                            <div className='sw-navbar-item'>
                                <Link className="sw-navbar-link" to='/'> Documentation </Link>
                            </div>
                            <div className='sw-navbar-item'>
                                <Link className="sw-navbar-link" to='/'> Components </Link>
                            </div>
                            <div className='sw-navbar-item'>
                                <Link className="sw-navbar-link" to='/'> Fonts </Link>
                            </div>
                            <div className='sw-navbar-item'>
                                <Link className="sw-navbar-link" to='/'> Spacing </Link>
                            </div>
                            <div className='sw-navbar-item'>
                                <Link className="sw-navbar-link" to='/'> Utilities </Link>
                            </div>
                            <div className='sw-navbar-item'>
                                <Link className="sw-navbar-link" to='/'> About </Link>
                            </div>
                        </nav>
                    </div>
                </div>

                <div className='sw-row p-5 f-12'>Made with good intentions for people Powered by Agust Enterprises</div>
            </div>
        </div>
    )
}

export default Footer;